body {
  margin: 0;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  display: block;
}

nav ul {
  align-items: center;
  list-style: none;
  display: flex;
}

nav ul li:not(:last-child) {
  margin-right: 20px;
}

nav ul li a {
  color: #000;
  text-transform: uppercase;
  text-align: center;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: flex;
}

nav ul li a:hover {
  color: #00acdc;
}

nav ul a.is-active {
  color: #00acdc;
  position: relative;
}

[data-qa="hover"]:hover {
  color: #00acdc;
}

nav ul li a.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  flex-shrink: 0;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

